/*-- Codes Snippet --*/

div.highlight,
figure.highlight,
.highlighter-rouge {
  background: #f7f7f7;
}

.highlight {
  overflow: auto;
  -moz-box-shadow: inset 0 0 2px #e9ecef;
  -webkit-box-shadow: inset 0 0 2px #e9ecef;
  box-shadow: inset 0 0 2px #e9ecef;
}

.highlight .lineno {
  padding: .8rem .5rem;
  min-width: 2rem;
  text-align: right;
  color: rgba(140, 134, 126, 0.67);
  background: rgb(234, 236, 242);
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}
.highlight table {
  padding: 0;
  border: 0;
}

.highlight td {
  padding: 0;
  border: 0;
}

.highlight pre {
  margin-bottom: 0;
  font-size: .85rem;
  line-height: 1.4rem;
  word-wrap: normal;  /* Fixed Safari overflow-x */
}

.highlight table td pre {
  overflow: visible;  /* Fixed iOS safari overflow-x */
  word-break: normal; /* Fixed iOS safari linenos code break */
}

/* Override BS Inline-code style */
.highlighter-rouge {
  color: black;
  margin-bottom: 1.2em;
}

code {
  -webkit-hyphens: none;
  -ms-hyphens: none;
  -moz-hyphens: none;
  hyphens: none;
}

code.highlighter-rouge {
  padding: 2px 4px;
  margin: 0 .3rem;
  border-radius: 4px;
  border: 1px solid #e9ecef;
}

td.rouge-code {
  padding-left: 1rem;
  padding-right: 1rem;
}

/* code snippet radius */
.highlighter-rouge,
.highlight {
  border-radius: 5px;
}


/* Hide line numbers for code snippets without specific language */
div[class^='highlighter-rouge'] pre.lineno {
  display: none;
}
div[class^='highlighter-rouge'] td.rouge-code {
  padding: .8rem 1rem;
}
